* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  padding: 40px;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  padding: 0px;
}

.country-card {
  display: flex;
  align-content: center;
  align-items: center;
}

.country-card_desc {
  font-size: 18px;
  font-style: normal;
  font-weight: lighter;
}

.country-card_img {
  display: block;
  width: 50px;
  height: 30px;
  margin-right: 10px;
}
